.main-content {
  flex-direction: column;
  gap: 40px;
  max-width: 1180px;
  margin: 0 auto;
  padding: 40px 20px 100px;
  display: flex;
}

.filters-bar {
  background-color: #fff;
  border-radius: 14px;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.filters-bar__left-content {
  gap: 20px;
  display: flex;
}

.filters-bar__mode {
  color: var(--primary-grey-dark);
  font-family: var(--font-family-dela-gothic);
  letter-spacing: -1.04px;
  cursor: pointer;
  align-items: center;
  gap: 10px;
  font-size: 26px;
  font-weight: 400;
  line-height: 100%;
  display: flex;
}

.filters-bar__mode_selected {
  color: var(--primary-black);
}

.filters-bar__right-content {
  gap: 10px;
  display: flex;
}

.filters-bar__filter {
  font-weight: var(--font-weight-bold);
  letter-spacing: -.64px;
  cursor: pointer;
  border-radius: 14px;
  align-items: center;
  gap: 14px;
  padding-right: 28px;
  line-height: 100%;
  display: flex;
}

.filters-bar__filter_pink {
  color: var(--primary-pink-dark);
  background-color: var(--primary-pink-light);
}

.filters-bar__filter_cyan {
  color: var(--primary-cyan-dark);
  background-color: var(--primary-cyan-light);
}

.filters-bar__settings {
  background-color: var(--primary-grey-light);
  border-radius: 4px;
  width: 44px;
  height: 44px;
  padding: 8px;
}

.language-levels-tabbar {
  justify-content: center;
  align-items: center;
  gap: 40px;
  display: flex;
}

.language-levels-tab {
  cursor: pointer;
  align-items: center;
  gap: 12px;
  padding-bottom: 14px;
  display: flex;
}

.language-levels-tab_selected {
  border-bottom: 2px solid var(--primary-black);
}

.language-levels-tab__level {
  color: var(--primary-grey-dark);
  font-family: var(--font-family-dela-gothic);
  letter-spacing: -1.04px;
  font-size: 26px;
  font-weight: 400;
  line-height: 100%;
}

.language-levels-tab__level_selected {
  color: var(--primary-black);
}

.tag {
  font-size: var(--font-size-xs);
  letter-spacing: -.48px;
  border-radius: 4px;
  padding: 4px 8px;
  line-height: 140%;
}

.tag_green {
  color: var(--secondary-green);
  background-color: var(--secondary-green-faded);
}

.tag_yellow {
  color: var(--secondary-yellow);
  background-color: var(--secondary-yellow-faded);
}

.tag_orange {
  color: var(--secondary-orange);
  background-color: var(--secondary-orange-faded);
}

.tag_selected {
  color: var(--primary-grey-light);
}

.tag_green.tag_selected {
  background-color: var(--secondary-green);
}

.tag_yellow.tag_selected {
  background-color: var(--secondary-yellow);
}

.tag_orange.tag_selected {
  background-color: var(--secondary-orange);
}

.cards {
  flex-direction: column;
  justify-content: center;
  gap: 40px;
  margin: 0 auto;
  display: flex;
}

.card {
  background: #fff;
  border-radius: 14px;
  max-width: 940px;
  display: flex;
  box-shadow: 0 22.8981px 45.7963px -11.4491px #34292714;
}

.card__image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 14px 0 0 14px;
  width: 620px;
  position: relative;
}

.card__review {
  color: #fff;
  cursor: pointer;
  background: #000000b3;
  border-radius: 8px 0 0 8px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 12px;
  padding: 8px;
  display: flex;
  position: absolute;
  top: 40px;
  right: 0;
}

.card__review > * {
  gap: 8px;
  display: flex;
}

.card__content {
  flex-direction: column;
  gap: 20px;
  padding: 40px;
  display: flex;
}

.card__header {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.card__title {
  justify-content: space-between;
  align-self: stretch;
  align-items: flex-start;
  display: flex;
}

.card__word {
  font-family: var(--font-family-dela-gothic);
  letter-spacing: -1.28px;
  gap: 12px;
  font-size: 32px;
  font-weight: 400;
  line-height: 100%;
  display: flex;
}

.card__audio {
  background-color: var(--primary-grey-light);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 4px;
  display: flex;
}

.card__word > span:last-child {
  color: var(--primary-grey-dark);
}

.card__subtitle {
  align-items: center;
  gap: 10px;
  display: flex;
}

.card__subtitle > span:first-child {
  font-size: var(--font-size-l);
  letter-spacing: .72px;
  line-height: 140%;
}

.card__description {
  color: var(--primary-black);
  font-weight: var(--font-weight-light);
  letter-spacing: -.64px;
  flex-direction: column;
  gap: 12px;
  line-height: 150%;
  display: flex;
}

.card__description > span:last-child {
  color: var(--primary-grey-dark);
}

.card__actions {
  gap: 20px;
  display: flex;
}

.pagination {
  justify-content: center;
  gap: 24px;
  display: flex;
}

.pagination__item {
  color: var(--secondary-cyan);
  font-weight: var(--font-weight-bold);
  letter-spacing: -.64px;
  border: 3px solid var(--secondary-cyan);
  cursor: pointer;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  padding: 14px;
  line-height: 100%;
  display: flex;
}

.pagination__item_selected {
  color: var(--primary-grey-light);
  border: 3px solid var(--primary-cyan-dark);
  background: var(--primary-cyan-dark);
}

.pagination__ellipsis {
  color: var(--primary-grey-dark);
  font-size: var(--font-size-l);
  letter-spacing: .72px;
  flex-direction: column;
  justify-content: flex-end;
  align-self: stretch;
  line-height: 140%;
  display: flex;
}

@media (width <= 1080px) {
  .main-content {
    gap: 30px;
    padding: 20px 20px 40px;
  }

  .filters-bar {
    flex-direction: column;
    gap: 20px;
    padding: 20px;
  }

  .filters-bar__left-content {
    gap: 12px;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
  }

  .filters-bar__left-content::-webkit-scrollbar {
    display: none;
  }

  .filters-bar__mode {
    font-size: 20px;
  }

  .filters-bar__right-content {
    gap: 8px;
  }

  .filters-bar__filter {
    height: 34px;
    font-size: 12px;
  }

  .filters-bar__filter > img {
    width: 34px;
    height: 34px;
  }

  .filters-bar__settings {
    width: 34px;
    height: 34px;
    padding: 0;
  }

  .language-levels-tabbar {
    justify-content: flex-start;
    gap: 26px;
    width: 100%;
    overflow-x: auto;
  }

  .language-levels-tabbar::-webkit-scrollbar {
    display: none;
  }

  .language-levels-tab {
    padding-bottom: 10px;
  }

  .language-levels-tab__level {
    font-size: var(--font-size-l);
  }

  .tag {
    padding: 3px 6px;
    font-size: 10px;
  }

  .cards {
    gap: 30px;
  }

  .card {
    flex-direction: column;
  }

  .card__image {
    background-size: cover;
    border-radius: 14px 14px 0 0;
    width: calc(100vw - 40px);
    height: 137px;
  }

  .card__content {
    gap: 12px;
    padding: 20px;
  }

  .card__word {
    flex-direction: column;
    gap: 4px;
    font-size: 24px;
  }

  .card__word > span:nth-child(2) {
    display: none;
  }

  .card__subtitle > span:first-child, .card__description {
    font-size: var(--font-size-xs);
  }

  .card__actions {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .card__review {
    font-size: var(--font-size-xs);
    top: inherit;
    bottom: 20px;
  }

  .card__review > * {
    align-items: center;
  }

  .pagination {
    display: none;
  }
}

